<template>
  <div class="ordersummary" id="ordersummary">
    <div class="tac">
      <el-aside width="100%" style="background-color: rgb(238, 241, 246)">
        <el-menu
          :unique-opened="true"
          @select="handleSelect"
          active-text-color="#ffd04b"
          default-active="1-1"
        >
          <el-submenu
            v-for="(item, idx) in leftMenu"
            :key="idx"
            :index="idx + 1 + ''"
          >
            <template slot="title">
              <i class="el-icon-message"></i>
              <span>{{ item.title }}</span>
            </template>
            <el-menu-item-group>
              <el-menu-item
                v-for="(singleItem, j) in item.children"
                :key="j"
                :index="idx + 1 + '-' + (j + 1)"
              >
                {{ singleItem.name }}
              </el-menu-item>
            </el-menu-item-group>
          </el-submenu>
        </el-menu>
      </el-aside>
    </div>
    <div class="right_table">
      <div v-if="defaultSku" class="center">
        <el-table
          ref="table_ordercom"
          :data="all_data"
          v-loading="loading"
          element-loading-text="拼命加载中"
          element-loading-spinner="el-icon-loading"
          element-loading-background="rgba(0, 0, 0, 0.3)"
          max-height="750"
          class="all_data_table"
          border
        >
          <el-table-column
            fixed
            prop="spu.value[0].value"
            label="SPU"
            width="150"
          >
          </el-table-column>
          <el-table-column
            v-if="sku"
            fixed
            prop="sku.value[0].value"
            label="SKU"
            width="150"
          ></el-table-column>
          <el-table-column
            fixed
            prop="fba.value[0].value"
            label="FBA"
            width="55"
            align="center"
          ></el-table-column>
          <el-table-column
            fixed
            v-if="fbm"
            prop="fbm.value[0].value"
            label="FBM"
            width="60"
            align="center"
          ></el-table-column>
          <el-table-column
            fixed
            v-if="zj"
            prop="zj.value[0].value"
            label="总计"
            width="60"
            align="center"
          ></el-table-column>
          <el-table-column
            v-for="(item, index) in all_date[0]"
            :key="index"
            :label="item.name"
            width="90"
            align="center"
          >
            <template slot-scope="scope">
              <span
                v-if="scope.row.all_date[index].value[0].color == 'white'"
                class="white_span"
                >{{ scope.row.all_date[index].value[0].value }}</span
              >
              <span
                v-else
                :style="{
                  background: scope.row.all_date[index].value[0].color
                }"
                style="display:inline-block;width:100%"
                >{{ scope.row.all_date[index].value[0].value }}</span
              >
            </template>
          </el-table-column>
          <el-table-column
            class="padding_column"
            label=""
            v-for="(item, index) in paddinglist"
            :key="index + '-'"
          >
          </el-table-column>
        </el-table>
      </div>
      <div
        class="operactor_table center"
        v-else-if="!defaultSku && !eu_operator"
      >
        <el-table :data="operator_data" max-height="720" border>
          <el-table-column
            prop="date"
            label="日期"
            width="100"
            align="center"
          ></el-table-column>
          <el-table-column
            prop="ffl"
            label="广告访问量"
            width="100"
            align="center"
          ></el-table-column>
          <el-table-column
            prop="yye"
            label="营业额"
            width="100"
            align="center"
          ></el-table-column>
          <el-table-column
            prop="gghf"
            label="广告花费"
            width="100"
            align="center"
          ></el-table-column>
          <el-table-column
            prop="ccb"
            label="投入产出比"
            width="100"
            align="center"
          ></el-table-column>
        </el-table>
      </div>
      <div class="operactor_table center" v-else>
        <div v-for="(item, i) in eu_operator_names" :key="i">
          <h2 @click="changeTableShow(i)">{{ item }}</h2>
          <div v-show="eu_showList[i]">
            <el-table :data="eu_operator_list[i]" border>
              <el-table-column
                prop="date"
                label="日期"
                width="100"
              ></el-table-column>
              <el-table-column
                prop="ffl"
                label="广告访问量"
                width="100"
              ></el-table-column>
              <el-table-column
                prop="yye"
                label="营业额"
                width="100"
              ></el-table-column>
              <el-table-column
                prop="gghf"
                label="广告花费"
                width="100"
              ></el-table-column>
              <el-table-column
                prop="ccb"
                label="投入产出比"
                width="100"
              ></el-table-column>
            </el-table>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      request_url: "",
      leftMenu: [],
      // 总计
      show_zj: false, // 页数第二页，类型为sku，spu，订单及订单汇总
      zj_table_data: [],
      // 运营
      operator: false,
      eu_operator: false,
      defaultSku: true,
      loading: false,

      titlelist: [
        {
          title: "胤佑美国",
          children: [
            "胤佑美国sku订单汇总",
            "胤佑美国spu订单汇总",
            "胤佑美国每日运营概要"
          ]
        },
        {
          title: "爱瑙美国",
          children: [
            "爱瑙美国sku订单汇总",
            "爱瑙美国spu订单汇总",
            "爱瑙美国每日运营概要"
          ]
        },
        {
          title: "京汇欧洲",
          children: [
            "京汇欧洲sku订单汇总",
            "京汇欧洲spu订单汇总",
            "京汇欧洲每日运营概要",
            "京汇欧洲德国订单",
            "京汇欧洲法国订单",
            "京汇欧洲西班牙订单",
            "京汇欧洲意大利订单"
          ]
        },
        {
          title: "京汇英国",
          children: [
            "京汇英国sku订单汇总",
            "京汇英国spu订单汇总",
            "京汇英国每日运营概要"
          ]
        },
        {
          title: "中睿欧洲",
          children: [
            "中睿欧洲sku订单汇总",
            "中睿欧洲spu订单汇总",
            "中睿欧洲每日运营概要",
            "中睿欧洲德国订单",
            "中睿欧洲法国订单",
            "中睿欧洲西班牙订单",
            "中睿欧洲意大利订单"
          ]
        },
        {
          title: "中睿英国",
          children: [
            "中睿英国sku订单汇总",
            "中睿英国spu订单汇总",
            "中睿英国每日运营概要"
          ]
        },
        {
          title: "爱瑙欧洲",
          children: [
            "爱瑙欧洲sku订单汇总",
            "爱瑙欧洲spu订单汇总",
            "爱瑙欧洲每日运营概要",
            "爱瑙欧洲德国订单",
            "爱瑙欧洲法国订单",
            "爱瑙欧洲西班牙订单",
            "爱瑙欧洲意大利订单"
          ]
        },
        {
          title: "爱瑙英国",
          children: [
            "爱瑙英国sku订单汇总",
            "爱瑙英国spu订单汇总",
            "爱瑙英国每日运营概要"
          ]
        },

        {
          title: "胤佑加拿大",
          children: [
            "胤佑加拿大sku订单汇总",
            "胤佑加拿大spu订单汇总",
            "胤佑加拿大每日运营概要"
          ]
        },
        {
          title: "爱瑙加拿大",
          children: [
            "爱瑙加拿大sku订单汇总",
            "爱瑙加拿大spu订单汇总",
            "爱瑙加拿大每日运营概要"
          ]
        },
        {
          title: "胤佑日本",
          children: [
            "胤佑日本sku订单汇总",
            "胤佑日本spu订单汇总",
            "胤佑日本每日运营概要"
          ]
        },
        {
          title: "胤佑澳洲",
          children: [
            "胤佑澳洲sku订单汇总",
            "胤佑澳洲spu订单汇总",
            "胤佑澳洲每日运营概要"
          ]
        },
        {
          title: "胤佑墨西哥",
          children: [
            "胤佑墨西哥sku订单汇总",
            "胤佑墨西哥spu订单汇总",
            "胤佑墨西哥每日运营概要"
          ]
        }
      ],
      spu: {},
      link: [],
      fba: {},
      datalist: [],
      fbm: {},
      sku: {},
      zj: {},
      all_data: [],
      all_date: [],
      name: "",
      area: "",
      operator_data: [], // 非欧洲运营
      eu_operator_data: {},
      // 欧洲运营
      eu_account: 0, //欧洲运营国家个数
      eu_showList: [], //对应的国家表格是否显示隐藏
      eu_operator_list: [], // 欧洲运营
      eu_operator_names: [], // 欧洲运营国家列表
      paddinglist: [],
      // FBM、FBA、总计各自总计
      FBA_ZJ: 0,
      FBM_ZJ: 0,
      ZJ_ZJ: 0
    };
  },
  computed: {
    dailyMenu() {
      return this.url === "server/databases/get_everyday_sku_order_summary/"
        ? true
        : false;
    }
  },
  methods: {
    changeTableShow(idx) {
      this.$set(this.eu_showList, idx, !this.eu_showList[idx]);
    },
    changeCountryName() {
      this.eu_operator_names.forEach((item, i) => {
        switch (item) {
          case "de":
            this.eu_operator_names[i] = "德国";
            break;
          case "eu":
            this.eu_operator_names[i] = "欧洲";
            break;
          case "fr":
            this.eu_operator_names[i] = "法国";
            break;
          case "it":
            this.eu_operator_names[i] = "意大利";
            break;
          case "es":
            this.eu_operator_names[i] = "西班牙";
            break;
        }
      });
    },
    handleSelect(key) {
      console.log(1111);
      this.show_zj = false;

      const keyArr = key.split("-");

      let firstIdx = parseInt(keyArr[0]) - 1;
      let secIdx = parseInt(keyArr[1]) - 1;
      let name = this.leftMenu[firstIdx].children[secIdx].name;
      let area = this.leftMenu[firstIdx].children[secIdx].area;
      let request_url = this.leftMenu[firstIdx].children[secIdx].url;
      console.log("打印参数", name, area, request_url);
      if (name === "广告报告上传") {
        this.$router.push("/AdvertisingReportUpload");
      } else {
        // 触发父级事件
        // todo
        // this.$emit("initPage", name, area, request_url);
        this.getTableData(name, area, request_url);
        this.name = name;
        this.area = area;
        this.request_url = request_url;
      }
    },
    getTableData(name, area, url) {
      this.loading = true;
      let queryData = { name, area };

      if (this.month) {
        queryData["dates"] = this.month;
      }
      // console.log("页数", this.page);
      // if (this.page) {
      //   queryData["page"] = this.page;
      // }

      // if (this.page) {
      //   console.log("请求的page", this.page);
      //   queryData["page"] = this.page;
      // }
      let that = this;
      // let url = "";
      let _url2 = this._url2;
      // 清空数组
      this.operator_data = [];
      this.eu_operator_list = [];
      this.eu_operator_names = [];

      this.all_data = [];
      this.all_date = [];

      console.log("name,area", name, area);
      if (name.indexOf("运营概要") > -1) {
        if (name.indexOf("欧洲") > -1) {
          // 欧洲运营概要
          // _url2 = "http://106.53.250.215:8003/";
          // url = "get_eu_gy_data/";
          this.eu_operator = true;
          this.operator = false;
          this.defaultSku = false;
          this.$axios
            .get(`${this._url2}${url}`, {
              params: {
                name,
                area
                // page: this.page
              }
            })
            .then(res => {
              // if (res.data.code !== 200) {
              //   alert("网页出错，请联系IT管理员！");
              //   return;
              // }
              // if (res.data.total_num) {
              //   // todo
              //   this.$emit("getTotalNum", res.data.total_num, name, area, url);
              // }
              console.log(res.data);
              if (res.data.code === 500) {
                alert(res.data.msg);
                return;
              }
              console.log("欧洲运营数据", res.data);
              this.eu_operator_data = res.data || {};
              // 拿到国家键
              let keys = Object.keys(this.eu_operator_data);
              this.eu_showList = [];
              keys.forEach((i, index) => {
                if (i !== "link") {
                  this.eu_showList.push(true);
                  // 切换成中文
                  this.eu_operator_names.push(i);
                  // 将对应的简写替换成中文
                  this.changeCountryName();
                  this.eu_operator_list.push(this.eu_operator_data[i]);
                }
              });
              console.log(this.eu_operator_names, this.eu_operator_list);
            });
        } else {
          // 非欧洲运营概要
          // _url2 = "http://106.53.250.215:8003/";
          // url = "get_gy_data/";
          this.eu_operator = false;
          this.operator = true;
          this.defaultSku = false;
          this.$axios
            .get(`${this._url2}${url}`, {
              params: {
                name,
                area
                // page: this.page
              }
            })
            .then(res => {
              if (res.data.code === 500) {
                alert(res.data.msg);
                return;
              }
              // if (res.data.total_num) {
              //   // todo
              //   this.$emit("getTotalNum", res.data.total_num, name, area, url);
              // }

              this.operator_data = res.data.data.ot;
            });
        }
      } else if (name.indexOf("订单汇总") > -1) {
        this.eu_operator = false;
        this.operator = false;
        this.defaultSku = true;

        // url = this.url;
        this.$axios
          .get(_url2 + url, {
            params: queryData
          })
          .then(res => {
            // 这里传进来一个总数，传给父组件。

            if (res.data.code === 500) {
              alert(res.data.msg);
              return;
            }

            // if (res.data.total_num) {
            //   this.$emit("getTotalNum", res.data.total_num, name, area, url);
            // }
            that.spu = res.data.spu;
            that.link = res.data.link;
            that.fba = res.data.fba;
            that.datalist = res.data.data_list;
            that.sku = res.data.sku;
            console.log("sku输入法", that.sku);
            that.zj = res.data.zj;
            that.fbm = res.data.fbm;
            let all_obj = [];
            let fba = {};
            let fbm = {};
            let zj = {};
            let sku = {};
            let spu = {};
            let date_obj = [];
            if (res.data.data_list.length < 1) {
              let length = that.fbm.value.length;
              let value0 = [];
              for (let i = 0; i < length; i++) {
                value0.push({ value: "", color: "" });
              }
              that.datalist.push({ name: "", value: value0 });
            }
            for (var i = 0; i < res.data.spu.value.length; i++) {
              for (var j = 0; j < that.datalist.length; j++) {
                date_obj.push({
                  name: that.datalist[j].name,
                  value: [
                    {
                      color: that.datalist[j].value[i].color,
                      value: that.datalist[j].value[i].value
                    }
                  ]
                });
              }
              if (that.fba.value && !that.fba.value[0].color) {
                fba = {
                  name: that.fba.name,
                  value: [
                    {
                      color: "",
                      value: that.fba.value[i]
                    }
                  ]
                };
              } else {
                fba = {
                  name: that.fba.name,
                  value: [
                    {
                      color: that.fba.value[i].color,
                      value: that.fba.value[i].value
                    }
                  ]
                };
              }
              if (that.fbm.value && !that.fbm.value[0].color) {
                fbm = {
                  name: that.fbm.name,
                  value: [
                    {
                      color: "",
                      value: that.fbm.value[i]
                    }
                  ]
                };
              } else {
                fbm = {
                  name: that.fbm.name,
                  value: [
                    {
                      color: that.fbm.value[i].color,
                      value: that.fbm.value[i].value
                    }
                  ]
                };
              }
              if (that.zj.value && !that.zj.value[0].color) {
                zj = {
                  name: that.zj.name,
                  value: [
                    {
                      color: "",
                      value: that.zj.value[i]
                    }
                  ]
                };
              } else {
                zj = {
                  name: that.zj.name,
                  value: [
                    {
                      color: that.zj.value[i].color,
                      value: that.zj.value[i].value
                    }
                  ]
                };
              }
              if (that.sku) {
                sku = {
                  name: that.sku.name,
                  value: [
                    {
                      color: that.sku.value[i].color,
                      value: that.sku.value[i].value
                    }
                  ]
                };
              }
              if (that.spu.value && !that.spu.value[0].color) {
                spu = {
                  name: that.spu.name,
                  value: [
                    {
                      color: "",
                      value: that.spu.value[i]
                    }
                  ]
                };
              } else {
                spu = {
                  name: that.spu.name,
                  value: [
                    {
                      color: that.spu.value[i].color,
                      value: that.spu.value[i].value
                    }
                  ]
                };
              }
              if (!sku.name) {
                that.all_data.push({
                  fba: fba,
                  fbm: fbm,
                  zj: zj,
                  spu: spu,
                  all_date: date_obj
                });
              } else {
                that.all_data.push({
                  fba: fba,
                  fbm: fbm,
                  zj: zj,
                  sku: sku,
                  spu: spu,
                  all_date: date_obj
                });
              }

              that.all_date.push(date_obj);
              date_obj = [];
            }

            this.loading = false;
            // if (this.page === 1) {
            //   if (this.name.indexOf("SKU") > -1) {
            //     // sku订单汇总
            //     this.zj_table_data = this.all_data.slice(0, 2);
            //     console.log(this.zj_table_data);
            //   } else {
            //     console.log("非sku");
            //     this.zj_table_data = this.all_data.slice(0, 1);
            //   }
            // }
            // 将zj_table_data追加到all_data的前面
            // console.log("显示总计行", this.show_zj);
            // if (this.show_zj) {
            //   this.all_data = this.zj_table_data.concat(this.all_data);
            // }

            // 新增列
            let len = 0;
            if (this.name.indexOf("SKU") > -1) {
              // spu页面
              len = this.all_date[0].length - 4;
            } else {
              len = this.all_date[0].length - 3;
            }
            for (let i = 0; i < len; i++) {
              this.$set(this.paddinglist, i, "");
            }

            // 计算FBA、FBM、总计各自的总和
            this.FBA_ZJ = 0;
            this.FBM_ZJ = 0;
            this.ZJ_ZJ = 0;
            for (let key in this.all_data) {
              let item = this.all_data[key];

              let FBA = item.fba ? item.fba : 0;
              let FBM = item.fbm ? item.fbm : 0;
              let ZJ = item.zj ? item.zj : 0;
              if (
                FBA &&
                FBA.value[0].value !== " " &&
                !isNaN(FBA.value[0].value)
              ) {
                this.FBA_ZJ += parseInt(FBA.value[0].value);
              }

              if (
                FBM &&
                FBM.value[0].value !== " " &&
                !isNaN(FBM.value[0].value)
              ) {
                this.FBM_ZJ += parseInt(FBM.value[0].value);
              }
              if (
                ZJ &&
                ZJ.value[0].value !== " " &&
                !isNaN(ZJ.value[0].value)
              ) {
                this.ZJ_ZJ += parseInt(ZJ.value[0].value);
              }
            }
            let ZJ_row_2;
            if (this.name.indexOf("SKU") > -1) {
              //sku订单汇总
              ZJ_row_2 = this.all_data[1];
            } else {
              // spu订单汇总
              ZJ_row_2 = this.all_data[0];
            }
            if (ZJ_row_2.fba) {
              this.$set(ZJ_row_2.fba, "value", [
                {
                  color: "rgb(0, 176, 80)",
                  value: this.FBA_ZJ
                }
              ]);
            }
            if (ZJ_row_2.fbm) {
              this.$set(ZJ_row_2.fbm, "value", [
                {
                  color: "rgb(0, 176, 80)",
                  value: this.FBM_ZJ
                }
              ]);
            }
            if (ZJ_row_2.zj) {
              this.$set(ZJ_row_2.zj, "value", [
                {
                  color: "rgb(0, 176, 80)",
                  value: this.ZJ_ZJ
                }
              ]);
            }
          });
      } else {
        // url = "get_eu_order_data/";
        this.eu_operator = false;
        this.operator = false;
        this.defaultSku = true;
        this.eu_operator = false;
        this.operator = false;
        this.defaultSku = true;

        this.$axios
          .get(`${this._url2}${url}`, {
            params: {
              name,
              area
              // page: this.page
            }
          })
          .then(res => {
            // 这里传进来一个总数，传给父组件。

            if (res.data.code === 500) {
              alert(res.data.msg);
              return;
            }
            // if (res.data.total_num) {
            //   this.$emit("getTotalNum", res.data.total_num, name, area, url);
            // }
            // this.all_data = [];
            // this.all_date = [];

            that.spu = res.data.spu;
            that.link = res.data.link;
            that.fba = res.data.fba;
            that.datalist = res.data.data_list;
            that.sku = res.data.sku;

            that.fbm = "";
            that.zj = "";
            let all_obj = [];
            let fba = {};
            let sku = {};
            let spu = {};
            let date_obj = [];
            if (res.data.data_list.length < 1) {
              let length = that.fba.value.length;
              let value0 = [];
              for (let i = 0; i < length; i++) {
                value0.push({ value: "", color: "" });
              }
              that.datalist.push({ name: "", value: value0 });
            }
            for (var i = 0; i < res.data.spu.value.length; i++) {
              for (var j = 0; j < that.datalist.length; j++) {
                date_obj.push({
                  name: that.datalist[j].name,
                  value: [
                    {
                      color: that.datalist[j].value[i].color,
                      value: that.datalist[j].value[i].value
                    }
                  ]
                });
              }
              if (that.fba.value && !that.fba.value[0].color) {
                fba = {
                  name: that.fba.name,
                  value: [
                    {
                      color: "",
                      value: that.fba.value[i]
                    }
                  ]
                };
              } else {
                fba = {
                  name: that.fba.name,
                  value: [
                    {
                      color: that.fba.value[i].color,
                      value: that.fba.value[i].value
                    }
                  ]
                };
              }
              if (that.sku) {
                sku = {
                  name: that.sku.name,
                  value: [
                    {
                      color: that.sku.value[i].color,
                      value: that.sku.value[i].value
                    }
                  ]
                };
              }
              if (that.spu.value && !that.spu.value[0].color) {
                spu = {
                  name: that.spu.name,
                  value: [
                    {
                      color: "",
                      value: that.spu.value[i]
                    }
                  ]
                };
              } else {
                spu = {
                  name: that.spu.name,
                  value: [
                    {
                      color: that.spu.value[i].color,
                      value: that.spu.value[i].value
                    }
                  ]
                };
              }

              that.all_data.push({
                fba: fba,
                sku: sku,
                spu: spu,
                all_date: date_obj
              });

              that.all_date.push(date_obj);
              date_obj = [];
            }

            // if (this.page === 1) {
            //   this.zj_table_data = this.all_data.slice(0, 1);
            // }

            this.loading = false;
            // 将zj_table_data追加到all_data的前面
            // console.log("显示总计行", this.show_zj);
            // if (this.show_zj) {
            //   this.all_data = this.zj_table_data.concat(this.all_data);
            // }
            // 新增列
            let len = 0;
            if (this.name.indexOf("SKU") > -1) {
              // spu页面
              len = this.all_date[0].length - 7;
            } else {
              len = this.all_date[0].length - 8;
            }
            for (let i = 0; i < len; i++) {
              this.$set(this.paddinglist, i, "");
            }

            // 计算FBA、FBM、总计各自的总和
            this.FBA_ZJ = 0;
            this.FBM_ZJ = 0;
            this.ZJ_ZJ = 0;
            for (let key in this.all_data) {
              let item = this.all_data[key];

              let FBA = item.fba ? item.fba : 0;
              let FBM = item.fbm ? item.fbm : 0;
              let ZJ = item.zj ? item.zj : 0;
              if (
                FBA &&
                FBA.value[0].value !== " " &&
                !isNaN(FBA.value[0].value)
              ) {
                this.FBA_ZJ += parseInt(FBA.value[0].value);
              }

              if (
                FBM &&
                FBM.value[0].value !== " " &&
                !isNaN(FBM.value[0].value)
              ) {
                this.FBM_ZJ += parseInt(FBM.value[0].value);
              }
              if (
                ZJ &&
                ZJ.value[0].value !== " " &&
                !isNaN(ZJ.value[0].value)
              ) {
                this.ZJ_ZJ += parseInt(ZJ.value[0].value);
              }
            }

            // 第二行表格数据
            let ZJ_row_2 = this.all_data[0];

            if (ZJ_row_2.fba) {
              this.$set(ZJ_row_2.fba, "value", [
                {
                  color: "rgb(0, 176, 80)",
                  value: this.FBA_ZJ
                }
              ]);
            }
            if (ZJ_row_2.fbm) {
              this.$set(ZJ_row_2.fbm, "value", [
                {
                  color: "rgb(0, 176, 80)",
                  value: this.FBM_ZJ
                }
              ]);
            }
            if (ZJ_row_2.zj) {
              this.$set(ZJ_row_2.zj, "value", [
                {
                  color: "rgb(0, 176, 80)",
                  value: this.ZJ_ZJ
                }
              ]);
            }
          });
      }
    }
  },
  //   可能需要传入的请求地址，url，参数，默认请求
  // props: ["url", "page", "month"],
  props: ["url", "month"],
  created() {
    let url = "server/databases/get_report_beside/";
    this.$axios
      .get(this._url2 + url, {
        params: {
          type: "history"
        }
      })
      .then(res => {
        if (res.data.code !== 200) {
          alert(res.data.msg);
          return;
        }
        console.log(res.data);
        this.leftMenu = res.data.data;
        this.name = this.leftMenu[0].children[0].name;
        this.area = this.leftMenu[0].children[0].area;
        this.request_url = this.leftMenu[0].children[0].url;
        // 发送请求
        console.log("发送请求", this.name, this.area, this.request_url);
        this.getTableData(this.name, this.area, this.request_url);
      });
  }
};
</script>

<style>
#ordersummary .tac {
  /* min-width: 196px; */
  flex: 0 0 250px;
}
#ordersummary .el-table .cell {
  padding: 0;
}
#ordersummary .el-table th:last-child {
  border-bottom: none;
}
#ordersummary .el-aside {
  height: 100%;
}

#ordersummary .operactor_table > .el-table {
  width: 500px;
}
#ordersummary .el-table--scrollable-x .el-table__body-wrapper {
  overflow-x: auto;
}
</style>
<style scoped>
#ordersummary {
  display: flex;
  overflow: hidden;
  height: 80vh;
  width: 100%;
  flex-wrap: nowrap;
}
#ordersummary .tac {
  max-height: 80vh;
  overflow-y: scroll;
}
#ordersummary .tac + div {
  flex: 1;
  /* overflow: scroll; */
  overflow: hidden;
}
#ordersummary >>> .el-table__body tr:hover td {
  /* background-color: rgb(234, 247, 224) !important; */
  background-color: #f5f7fa !important;
}
#ordersummary .operactor_table h2 {
  color: #000;
  padding: 5px 0;
  font-size: 20px;
}
#ordersummary .right_table {
  background: rgba(255, 255, 255, 0.9);
}
/* #ordersummary .center {
  width: 80vw;
  margin: 0 auto;
  padding: 0 20px;
} */
</style>



